import React from "react";
import ReactDOM from "react-dom";
import { HashRouter, Route, Switch, Redirect } from "react-router-dom";

// core components
import Admin from "layouts/Admin.js";
import Home from "layouts/Home.js";
import ItemDetails from "components/homeComponents/itemDetails/itemDetails.js";
import Rules from "components/homeComponents/rules/rules.js";
import Contacts from "components/homeComponents/contacts/contacts.js";
import Search from "components/homeComponents/search/search.js";

import "assets/css/material-dashboard-react.css?v=1.8.0";
import "bootstrap/dist/css/bootstrap.min.css";

//admin, tribunal, sindico, martillero, usuario (apostador)
ReactDOM.render(
  <body style={{ backgroundColor: "white" }}>
    <HashRouter basename="/">
      <Switch>
        <Route path="/admin" component={Admin} />
        <Route path="/home" component={Home} />
        <Route path="/bien/:id" component={ItemDetails} />
        <Route path="/info" component={Rules} />
        <Route path="/contacto" component={Contacts} />
        <Route from="/busqueda/:id" component={Search} />
        <Redirect from="/" to="/home" />
      </Switch>
    </HashRouter>
  </body>,
  document.getElementById("root")
);
