import React from "react";
import { makeStyles } from "@material-ui/core/styles";
//import tileData from "components/homeComponents/scrollGrid/tileData.js";
import ItemCard from "components/homeComponents/itemCard/ItemCard.js";
import { useFetchAll } from "components/homeComponents/scrollGrid/useFetchAll.js";
import normalDate from "components/homeComponents/normadDate.js";
import Grow from "@material-ui/core/Grow";

const useStyles = makeStyles(theme => ({
  root: {
    boxShadow: "none",
    display: "flex",
    flexWrap: "wrap",
    justifyContent: "center",
    overflow: "hidden",
    backgroundColor: theme.palette.background.paper
  },
  item: { margin: "4px" },
  icon: {
    color: "rgba(255, 255, 255, 0.54)"
  }
}));

export default function ItemBox(props) {
  const classes = useStyles();

  const subastasCba =
    "https://subasta.justiciacordoba.gob.ar/api/subastas_categoria/";
  const subastasSLuis =
    "https://subasta.justiciasanluis.gov.ar/api/subastas_categoria/";

  const [dataCba, loadingCba] = useFetchAll(subastasCba);
  const [dataSLuis, loadingSLuis] = useFetchAll(subastasSLuis);

  const data = [...dataCba, ...dataSLuis];

  console.log(loadingCba);
  console.log(loadingSLuis);

  // tileDataCat === [object : object.category === props.category ]
  const tileDataCat = data.filter(tile => tile.categoria === props.category);

  return (
    <div className={classes.root}>
      {tileDataCat.map(tile => (
        <Grow in={true}>
          <div className={classes.item}>
            <ItemCard
              image={tile.foto}
              title={tile.nombre}
              inicio={normalDate(tile.fecha_inicio_subasta)}
              termino={normalDate(tile.fecha_fin)}
              link={tile.link_sitio}
              precio={tile.precio_ultima_oferta}
              gavel="off"
              love="off"
            />
          </div>
        </Grow>
      ))}
    </div>
  );
}
