import React from "react";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
// core components
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import Table from "components/Table/Table.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import { Button } from "@material-ui/core";
import ArrowLeftIcon from "@material-ui/icons/ArrowLeft";
import ArrowRightIcon from "@material-ui/icons/ArrowRight";
import url from "views/url.js";

const styles = {
  cardCategoryWhite: {
    "&,& a,& a:hover,& a:focus": {
      color: "rgba(255,255,255,.62)",
      margin: "0",
      fontSize: "14px",
      marginTop: "0",
      marginBottom: "0"
    },
    "& a,& a:hover,& a:focus": {
      color: "#FFFFFF"
    }
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none",
    "& small": {
      color: "#777",
      fontSize: "65%",
      fontWeight: "400",
      lineHeight: "1"
    }
  }
};

const useStyles = makeStyles(styles);

const datos = jsonArr => {
  const id = jsonArr.map(json => json.perfil_id);
  const nombre = jsonArr.map(json => json.razon_social);
  const cuit = jsonArr.map(json => json.cuit);
  const representante = jsonArr.map(json => json.apoderado.nombre_completo);
  const cuitRepresentante = jsonArr.map(json => json.apoderado.identificacion);
  const acreditacion = jsonArr.map(json => json.apoderado.personeria);
  const estado = jsonArr.map(json => json.estado);

  var result = [];
  for (var i = 0; i < nombre.length; i++) {
    const arr = [
      id[i],
      nombre[i],
      cuit[i] ? "Activo" : "Inactivo",
      representante[i],
      cuitRepresentante[i],
      acreditacion[i],
      estado[i],

      "ANULAR"
    ];
    result.push(arr);
  }
  return result;
};
export default function Tribunales() {
  const classes = useStyles();

  const [newUrl, setNewUrl] = React.useState(url + "/get_personas_juridicas");
  const [dataPjuridicas, setDataPjuridicas] = React.useState([]);
  const [allInfo, setAllinfo] = React.useState([]);

  //fetchingData
  const fetchUrl = async () => {
    const json = await fetch(newUrl).then(response => response.json());
    setAllinfo(json);
    setDataPjuridicas(datos(json.results));
  };

  const fetchNext = async () => {
    const json = await fetch(allInfo.next).then(response => response.json());
    setAllinfo(json);
    setDataPjuridicas(datos(json.results));
  };

  const fetchPrevious = async () => {
    const json = await fetch(allInfo.previous).then(response =>
      response.json()
    );
    setAllinfo(json);
    setDataPjuridicas(datos(json.results));
  };

  React.useEffect(() => {
    fetchUrl();
    fetchNext();
    fetchPrevious();
    // eslint-disable-next-line
  }, []);

  const nextOrPreviousUrl = option => {
    if (option === "next") {
      setNewUrl(allInfo.next);
      fetchNext();
    } else {
      setNewUrl(allInfo.previous);
      fetchPrevious();
    }
  };

  return (
    <GridContainer>
      <GridItem xs={12} sm={12} md={12}>
        <Card>
          <CardHeader color="primary">
            <h4 className={classes.cardTitleWhite}>
              Personas Jur√≠dicas
              <Button style={{ color: "white", left: "55%" }}>
                {" "}
                Nueva persona jur√≠dica
              </Button>
            </h4>
          </CardHeader>
          <CardBody>
            <Table
              tableHeaderColor="primary"
              tableHead={[
                "ID",
                "Nombre",
                "CUIT/CUIL",
                "Representante",
                "CUIT/CUIL Representante",
                "Acreditaci√≥n personer√≠a",
                "Estado"
              ]}
              tableData={dataPjuridicas}
            />
          </CardBody>
          {/*This is the pagination menu */}
          <div>
            <Button onClick={() => nextOrPreviousUrl("previous")}>
              <ArrowLeftIcon />
            </Button>
            <Button onClick={() => nextOrPreviousUrl("next")}>
              <ArrowRightIcon />
            </Button>
          </div>
        </Card>
      </GridItem>
    </GridContainer>
  );
}
