import React from "react";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
// core components
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import Table from "components/Table/Table.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import { Button } from "@material-ui/core";
import url from "views/url.js";
import ArrowLeftIcon from "@material-ui/icons/ArrowLeft";
import ArrowRightIcon from "@material-ui/icons/ArrowRight";

const styles = {
  cardCategoryWhite: {
    "&,& a,& a:hover,& a:focus": {
      color: "rgba(255,255,255,.62)",
      margin: "0",
      fontSize: "14px",
      marginTop: "0",
      marginBottom: "0"
    },
    "& a,& a:hover,& a:focus": {
      color: "#FFFFFF"
    }
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none",
    "& small": {
      color: "#777",
      fontSize: "65%",
      fontWeight: "400",
      lineHeight: "1"
    }
  }
};

const useStyles = makeStyles(styles);

const acron = acron => {
  var result = "";
  switch (acron) {
    case "PO":
      result = "Porcentaje";
      break;
    case "FJ":
      result = "Monto Fijo";
      break;
    case "MB":
      result = "Mueble";
      break;
    case "RE":
      result = "Registrable";
      break;
    case "IM":
      result = "Inmueble";
      break;
    case "TO":
      result = "todos";
      break;
    default:
      result = acron;
  }
  return result;
};

const montoOrPorcentaje = (monto, porcentaje) => {
  if (monto === "0.0") {
    return porcentaje + " %";
  }
  return "$ " + monto;
};

const datos = jsonArr => {
  const nombre = jsonArr.map(json => json.nombre);
  const tipo = jsonArr.map(json => json.tipo);
  const tipoBien = jsonArr.map(json => json.tipo_bien);
  const monto = jsonArr.map(json => json.monto_comision);
  const porcentaje = jsonArr.map(json => json.porcentaje_comision);

  var result = [];
  for (var i = 0; i < nombre.length; i++) {
    const arr = [
      nombre[i],
      acron(tipo[i]),
      acron(tipoBien[i]),
      montoOrPorcentaje(monto[i], porcentaje[i])
    ];
    result.push(arr);
  }
  return result;
};

export default function ComisionesImpuestos() {
  const classes = useStyles();

  const [newUrl, setNewUrl] = React.useState(url + "/get_comisiones");
  const [dataComisiones, setDataComisiones] = React.useState([]);
  const [allInfo, setAllinfo] = React.useState([]);

  //fetchingData
  const fetchUrl = async () => {
    const json = await fetch(newUrl).then(response => response.json());
    setAllinfo(json);
    setDataComisiones(datos(json.results));
  };

  const fetchNext = async () => {
    const json = await fetch(allInfo.next).then(response => response.json());
    setAllinfo(json);
    setDataComisiones(datos(json.results));
  };

  const fetchPrevious = async () => {
    const json = await fetch(allInfo.previous).then(response =>
      response.json()
    );
    setAllinfo(json);
    setDataComisiones(datos(json.results));
  };

  React.useEffect(() => {
    fetchUrl();
    fetchNext();
    fetchPrevious();
    // eslint-disable-next-line
  }, []);

  const nextOrPreviousUrl = option => {
    if (option === "next") {
      setNewUrl(allInfo.next);
      fetchNext();
    } else {
      setNewUrl(allInfo.previous);
      fetchPrevious();
    }
  };
  return (
    <GridContainer>
      <GridItem xs={12} sm={12} md={12}>
        <Card>
          <CardHeader color="primary">
            <h4 className={classes.cardTitleWhite}>
              Comisiones e Impuestos{" "}
              <Button style={{ color: "white", left: "50%" }}>
                {" "}
                Nueva Comision
              </Button>
            </h4>
          </CardHeader>
          <CardBody>
            <Table
              tableHeaderColor="primary"
              tableHead={[
                "Nombre",
                "Tipo",
                "Tipo de Bien",
                "Monto / Porcentaje"
              ]}
              tableData={dataComisiones}
            />
          </CardBody>
          {/*This is the pagination menu */}
          <div>
            <Button onClick={() => nextOrPreviousUrl("previous")}>
              <ArrowLeftIcon />
            </Button>
            <Button onClick={() => nextOrPreviousUrl("next")}>
              <ArrowRightIcon />
            </Button>
          </div>
        </Card>
      </GridItem>
    </GridContainer>
  );
}
