import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardActionArea from "@material-ui/core/CardActionArea";
import CardContent from "@material-ui/core/CardContent";
import CardMedia from "@material-ui/core/CardMedia";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import Favorite from "@material-ui/icons/Favorite";
import Gavel from "@material-ui/icons/Gavel";
import Chip from "@material-ui/core/Chip";
import Avatar from "@material-ui/core/Avatar";

const useStyles = makeStyles({
  card: {
    width: 200,
    maxHeight: "400px"
  },
  media: {
    height: 200,
    width: 200
  }
});

const priceCommas = price => {
  return price.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
};

export default function ItemCard(props) {
  const classes = useStyles();
  const link = props.link;

  return (
    <div>
      <Card className={classes.card}>
        <CardActionArea>
          <a target="_blank" rel="noopener noreferrer" href={link}>
            <CardMedia className={classes.media} image={props.image} />
          </a>

          <CardContent>
            <Typography
              gutterBottom
              variant="h5"
              component="h2"
              style={{ fontSize: "10px" }}
            >
              {props.title}
            </Typography>
            <Typography variant="body2" color="textSecondary" component="p">
              Desde el {props.inicio}
            </Typography>
            <Typography variant="body2" color="textSecondary" component="p">
              Hasta el {props.termino}
            </Typography>
            <Typography align="center">
              <br />
              <Chip
                avatar={
                  <Avatar
                    style={{ color: "white", backgroundColor: "#f68823" }}
                  >
                    $
                  </Avatar>
                }
                label={priceCommas(props.precio)}
                style={{ color: "white", backgroundColor: "#ffb774" }}
              />
            </Typography>
            <br />

            {props.love === "off" ? null : (
              <IconButton style={{ color: "#f68823" }}>
                <Favorite />
              </IconButton>
            )}

            {props.gavel === "off" ? null : (
              <IconButton style={{ color: "#f68823" }}>
                <Gavel />
              </IconButton>
            )}
          </CardContent>
        </CardActionArea>
      </Card>
    </div>
  );
}
