import React from "react";
import NavbarHome from "components/homeComponents/navbar/NavbarHome.js";
import { useFetchAll } from "components/homeComponents/scrollGrid/useFetchAll.js";
import ItemCard from "components/homeComponents/itemCard/ItemCard.js";
import normalDate from "components/homeComponents/normadDate.js";
import { makeStyles } from "@material-ui/core/styles";
import Badge from "@material-ui/core/Badge";
import SearchIcon from "@material-ui/icons/Search";
import Grid from "@material-ui/core/Grid";
import Button from "@material-ui/core/Button";
import ButtonGroup from "@material-ui/core/ButtonGroup";
import Footer from "components/Footer/Footer.js";
import Typography from "@material-ui/core/Typography";
import CircularProgress from "@material-ui/core/CircularProgress";

const useStyles = makeStyles(theme => ({
  root: {
    display: "flex",
    flexWrap: "wrap",
    justifyContent: "center",
    overflow: "hidden",
    backgroundColor: theme.palette.background.paper
  },
  item: { margin: "4px" },
  icon: {
    color: "rgba(255, 255, 255, 0.54)"
  }
}));

export default function Search({ match }) {
  const [tokens, setTokens] = React.useState("search");
  const classes = useStyles();

  const subastasCba =
    "https://subasta.justiciacordoba.gob.ar/api/subastas_categoria/";
  const subastasSLuis =
    "https://subasta.justiciasanluis.gov.ar/api/subastas_categoria/";

  const [dataCba, loadingCba] = useFetchAll(subastasCba);
  const [dataSLuis, loadingSLuis] = useFetchAll(subastasSLuis);

  const data = [...dataCba, ...dataSLuis];

  console.log(loadingCba, loadingSLuis);
  var search = match.params.id;
  search = search.replace(" ", "");
  const regex = RegExp(search.toUpperCase() + "|" + search.toLowerCase());
  const tileDataCat = data.filter(tile => regex.test(tile.nombre));
  const cheap = data.filter(tile => tile.precio_ultima_oferta <= 5000);
  const medium = data.filter(
    tile =>
      tile.precio_ultima_oferta > 5000 && tile.precio_ultima_oferta < 30000
  );
  const expensive = data.filter(tile => tile.precio_ultima_oferta > 30000);

  const items = option => {
    return (
      <div>
        {loadingCba || loadingSLuis ? (
          <div>
            <CircularProgress
              style={{
                display: "block",
                marginLeft: "auto",
                marginRight: "auto",
                color: "#f68823"
              }}
            />
            <br />
          </div>
        ) : null}

        <Grid className={classes.root} container spacing={2}>
          {option.map(tile => (
            <div style={{ margin: "15px" }}>
              <ItemCard
                image={tile.foto}
                title={tile.nombre}
                inicio={normalDate(tile.fecha_inicio_subasta)}
                termino={normalDate(tile.fecha_fin)}
                link={tile.link_sitio}
                precio={tile.precio_ultima_oferta}
                gavel="off"
                love="off"
              />
            </div>
          ))}
        </Grid>
      </div>
    );
  };

  return (
    <React.Fragment>
      <NavbarHome searchDeactivate />
      <br />
      <div style={{ marginLeft: "50%" }}>
        <Badge badgeContent={tileDataCat.length} style={{ color: "#f68823" }}>
          <SearchIcon style={{ color: "#f68823" }} />
        </Badge>
      </div>
      <br />
      <div>
        <Grid item xs={12} md={6}>
          <Grid container spacing={2} direction="column" alignItems="center">
            <Grid item>
              <ButtonGroup
                variant="primary"
                size="small"
                aria-label="small contained button group"
                style={{ boxShadow: "none" }}
              >
                <Button
                  style={{ color: "grey" }}
                  onClick={() => setTokens("cheap")}
                >
                  0 a 5K{" "}
                </Button>
                <Button
                  style={{ color: "grey" }}
                  onClick={() => setTokens("medium")}
                >
                  5K a 30K{" "}
                </Button>
                <Button
                  style={{ color: "grey" }}
                  onClick={() => setTokens("expensive")}
                >
                  30K o m√°s mil{" "}
                </Button>
              </ButtonGroup>
            </Grid>
          </Grid>
        </Grid>
      </div>
      {loadingCba || loadingSLuis ? null : (
        <Typography
          align="center"
          style={{ margin: "12px", color: "grey", fontSize: "16px" }}
        >
          Se encontr√≥ {tileDataCat.length} resultados para "{match.params.id} "
          .{" "}
        </Typography>
      )}

      <br />
      {tokens === "cheap" ? items(cheap) : null}
      {tokens === "medium" ? items(medium) : null}
      {tokens === "expensive" ? items(expensive) : null}
      {!(tokens in ["expensive", "search", "cheap"])
        ? items(tileDataCat)
        : null}
      <br />
      <br />

      <br />
      <br />
      <br />
      <br />
      <br />
      <br />
      <br />
      <br />
      <br />
      <br />
      <br />
      <br />
      <br />
      <br />
      <br />
      <br />
      <Footer />
    </React.Fragment>
  );
}
