import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
//import tileData from "components/homeComponents/scrollGrid/tileData.js";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import CardHeader from "@material-ui/core/CardHeader";
import Avatar from "@material-ui/core/Avatar";
import Gavel from "@material-ui/icons/Gavel";
import ItemCard from "components/homeComponents/itemCard/ItemCard.js";
//import { useFetch } from "./useFetch";
import { useFetchAll } from "./useFetchAll";
import Button from "@material-ui/core/Button";
import ArrowLeft from "@material-ui/icons/ArrowLeft";
import ArrowRight from "@material-ui/icons/ArrowRight";
import normalDate from "components/homeComponents/normadDate.js";
import CircularProgress from "@material-ui/core/CircularProgress";

const useStyles = makeStyles(theme => ({
  fab: {
    margin: theme.spacing(1)
  },
  cardWhite: {
    borderRadius: "0px",
    boxShadow: "none",
    width: "100vw",
    backgroundColor: "white"
  },

  root: {
    display: "flex",
    flexWrap: "wrap",
    justifyContent: "space-around",
    backgroundColor: theme.palette.background.paper
  },
  gridList: {
    flexWrap: "nowrap",
    // Promote the list into his own layer on Chrome. This cost memory but helps keeping high FPS.
    transform: "translateZ(0)",
    overflow: "auto"
  },
  title: {
    color: "white"
  },
  titleBar: {
    background:
      "linear-gradient(to top, rgba(0,0,0,0.7) 0%, rgba(0,0,0,0.3) 70%, rgba(0,0,0,0) 100%)"
  },
  orangeAvatar: {
    margin: 10,
    color: "#fff",
    backgroundColor: "#f68823"
  }
}));

export default function ScrollGridHome(props) {
  const classes = useStyles();
  const cardColor = color => {
    if (color === "orange") {
      return classes.cardOrange;
    } else {
      return classes.cardWhite;
    }
  };

  const [url, setUrl] = React.useState(props.url);
  const [data, setData] = React.useState([]);
  const [loading, setLoading] = React.useState(true);

  //fetchingData
  async function fetchUrl() {
    const response = await fetch(url);
    const json = await response.json();
    setData(json.results);
    setLoading(false);
  }
  React.useEffect(() => {
    fetchUrl();
    // eslint-disable-next-line
  }, []);

  const [dataComplete, loadingNext] = useFetchAll(url);

  const nextUrl = () => {
    setUrl(dataComplete.next);
    fetchUrl();
    var elmnt = document.getElementById(props.name);
    elmnt.scrollLeft = -1000;
  };

  const previousUrl = () => {
    setUrl(dataComplete.previous);
    fetchUrl();
  };

  //just to avoid warnings
  console.log(dataComplete);
  console.log(loadingNext);
  console.log(loading);

  if (dataComplete.count === 0) {
    return "";
  } else {
    return (
      <div className={classes.root}>
        <Card className={cardColor(props.color)}>
          <CardHeader
            avatar={
              <Avatar aria-label="recipe" className={classes.orangeAvatar}>
                <Gavel />
              </Avatar>
            }
            title={props.name}
            style={{ color: "grey" }}
          />

          <CardContent>
            <Grid
              id={props.name}
              className={classes.gridList}
              container
              item="true"
              spacing={1}
            >
              {dataComplete.count > 10 ? (
                <Button onClick={() => previousUrl()}>
                  <ArrowLeft style={{ color: "#f68823" }} />
                </Button>
              ) : (
                <Button disabled></Button>
              )}
              {loading ? (
                <CircularProgress
                  style={{
                    display: "block",
                    marginLeft: "auto",
                    marginRight: "auto",
                    color: "#f68823"
                  }}
                />
              ) : null}
              {data.map(tile => (
                <Grid item>
                  <ItemCard
                    image={tile.foto}
                    title={tile.nombre}
                    inicio={normalDate(tile.fecha_inicio_subasta)}
                    termino={normalDate(tile.fecha_fin)}
                    link={tile.link_sitio}
                    precio={tile.precio_ultima_oferta}
                    gavel="off"
                    love="off"
                  />
                </Grid>
              ))}

              {dataComplete.count > 10 ? (
                <Button onClick={() => nextUrl()}>
                  <ArrowRight style={{ color: "#f68823" }} />
                </Button>
              ) : (
                <Button disabled></Button>
              )}
            </Grid>
          </CardContent>
        </Card>
      </div>
    );
  }
}
