import React from "react";
import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";

export default function AgregarMartillero() {
  const [open, setOpen] = React.useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  var nuevoMartillero = {
    perfil: " ",
    matricula: " ",
    matricula_publica: " ",
    telefono: " ",
    direccion: " ",
    localidad: " ",
    tipo_documento: " ",
    cuit_documento: " ",
    fecha_nacimiento: " ",
    cbu: " "
  };

  const [data, setData] = React.useState(nuevoMartillero);

  const setNewData = (option, e) => {
    switch (option) {
      case "perfil":
        setData({ perfil: e.target.value });
        break;

      case "matricula":
        setData({ matricula: e.target.value });
        break;

      case "matricula_publica":
        setData({ matricula_publica: e.target.value });

        break;

      case "telefono":
        setData({ telefono: e.target.value });

        break;

      case "direccion":
        setData({ direccion: e.target.value });

        break;

      case "localidad":
        setData({ localidad: e.target.value });

        break;

      case "tipo_documento":
        setData({ tipo_documento: e.target.value });

        break;

      case "cuit_documento":
        setData({ cuit_documento: e.target.value });

        break;

      case "fecha_nacimiento":
        setData({ fecha_nacimiento: e.target.value });
        break;

      case "cbu":
        setData({ cbu: e.target.value });
        break;
      default:
        break;
    }
    console.log(data);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <div>
      <Button variant="outlined" color="primary" onClick={handleClickOpen}>
        Nuevo Martillero
      </Button>
      <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="form-dialog-title"
      >
        <DialogTitle id="form-dialog-title">Nuevo Martillero</DialogTitle>
        <DialogContent>
          <TextField
            autoFocus
            margin="dense"
            id="Usuario"
            label="Usuario:"
            fullWidth
            onChange={e => setNewData("perfil", e)}
          />
          <TextField
            autoFocus
            margin="dense"
            id="Contrase√±a"
            label="Contrase√±a:"
            fullWidth
          />
          <TextField
            autoFocus
            margin="dense"
            id="Email"
            onChange={e => setNewData("perfil", e)}
            label="Email:"
            fullWidth
          />
          <TextField
            autoFocus
            margin="dense"
            id="Estado"
            label="Estado:"
            fullWidth
          />
          <TextField
            autoFocus
            margin="dense"
            id="Matricula"
            label="Matricula:"
            fullWidth
          />
          <TextField
            autoFocus
            margin="dense"
            id="Nombre"
            label="Nombre:"
            fullWidth
          />
          <TextField
            autoFocus
            margin="dense"
            id="Apellido"
            label="Apellido:"
            fullWidth
          />
          <TextField
            autoFocus
            margin="dense"
            id="Telefono"
            label="Telefono:"
            fullWidth
          />
          <TextField
            autoFocus
            margin="dense"
            id="Direcci√≥n"
            label="Direcci√≥n:"
            fullWidth
          />
          <TextField
            autoFocus
            margin="dense"
            id="Provincia"
            label="Provincia:"
            fullWidth
          />
          <TextField
            autoFocus
            margin="dense"
            id="Localidad"
            label="Localidad:"
            fullWidth
          />
          <TextField
            autoFocus
            margin="dense"
            id="Cuit/cuil"
            label="Cuit/cuil:"
            fullWidth
          />
          <TextField autoFocus margin="dense" id="CBU" label="CBU:" fullWidth />
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            Cerrar
          </Button>
          <Button onClick={handleClose} color="primary">
            Agregar
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
