import React from "react";
import Table from "components/Table/Table.js";
import Card from "components/Card/Card.js";
import Icon from "@material-ui/core/Icon";
import { makeStyles } from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import CardHeader from "components/Card/CardHeader.js";

const useStyles = makeStyles(theme => ({
  button: {
    margin: theme.spacing(1),
    boxShadow: "none"
  },
  input: {
    display: "none"
  }
}));

export default function SubastasElectronicasTabla(props) {
  const classes = useStyles();

  //estado funcional.
  const [clicked, setClicked] = React.useState("EN CURSO");

  //tablas
  const tablaEnCurso = (
    <Table
      tableHeaderColor="primary"
      tableHead={[
        "Expediente",
        "CBU",
        "Martillero",
        "Tribunal",
        "Fecha Inicio"
      ]}
      tableData={props.dataEnCurso}
    />
  );

  const tablaProximas = (
    <Table
      tableHeaderColor="primary"
      tableHead={["Expediente", "Martillero", "Tribunal", "Comienza"]}
      tableData={props.dataProximas}
    />
  );

  const tablaSuspendidas = (
    <Table
      tableHeaderColor="primary"
      tableHead={["Expediente", "Martillero", "Tribunal", "Motivo"]}
      tableData={props.dataEnCurso}
    />
  );

  const tablaPendientes = (
    <Table
      tableHeaderColor="primary"
      tableHead={["Expediente", "Martillero", "Tribunal", "Fecha fin"]}
      tableData={props.dataEnCurso}
    />
  );

  const tablaFinalizadas = (
    <Table
      tableHeaderColor="primary"
      tableHead={["Expediente", "Martillero", "Tribunal", "Termin√≥"]}
      tableData={props.dataEnCurso}
    />
  );

  const conditionalTable = state => {
    switch (state) {
      case "EN CURSO":
        return tablaEnCurso;
      case "PROXIMAS":
        return tablaProximas;
      case "SUSPENDIDAS":
        return tablaSuspendidas;
      case "PENDIENTES DE APROBACION":
        return tablaPendientes;
      case "FINALIZADAS":
        return tablaFinalizadas;
      default:
        return tablaEnCurso;
    }
  };

  return (
    <React.Fragment>
      <Card>
        <div>
          <CardHeader color="primary">
            <Button
              variant="contained"
              className={classes.button}
              onClick={() => setClicked("EN CURSO")}
            >
              <Icon>play_arrow</Icon>
              EN CURSO
            </Button>
            <Button
              variant="contained"
              color="primary"
              className={classes.button}
              onClick={() => setClicked("PROXIMAS")}
            >
              <Icon>skip_next</Icon>
              PROXIMAS
            </Button>
            <Button
              variant="contained"
              color="secondary"
              className={classes.button}
              onClick={() => setClicked("SUSPENDIDAS")}
            >
              <Icon>not_interested</Icon>
              SUSPENDIDAS
            </Button>
            <Button
              variant="contained"
              color="secondary"
              className={classes.button}
              onClick={() => setClicked("PENDIENTES DE APROBACION")}
            >
              <Icon>loop</Icon>
              PENDIENTES DE APROBACION
            </Button>
            <Button
              variant="contained"
              href="#contained-buttons"
              className={classes.button}
              onClick={() => setClicked("FINALIZADAS")}
            >
              <Icon>check</Icon>
              FINALIZADAS
            </Button>
          </CardHeader>
        </div>

        {conditionalTable(clicked)}
      </Card>
    </React.Fragment>
  );
}
