import React from "react";
import Card from "@material-ui/core/Card";
import Grid from "@material-ui/core/Grid";
import { makeStyles } from "@material-ui/core/styles";
import Categories from "components/homeComponents/itemList/categories.js";
import ItemBox from "components/homeComponents/itemList/ItemBox.js";

const useStyles = makeStyles({
  card: {
    maxWidth: "2000px",
    boxShadow: "none"
  }
});

export default function ItemList(props) {
  const classes = useStyles();

  const [category, setCategory] = React.useState("default");
  const handleClick = category => {
    setCategory(category);
  };

  return (
    <Card className={classes.card}>
      <Grid container spacing={2}>
        <Grid item>
          <Categories handleClick={handleClick} />
        </Grid>
        <Grid item className={classes.item}>
          <ItemBox category={category} />{" "}
        </Grid>
      </Grid>
    </Card>
  );
}
