import React from "react";
import Navbar from "react-bootstrap/Navbar";
import Nav from "react-bootstrap/Nav";
import IconButton from "@material-ui/core/IconButton";
import SearchIcon from "@material-ui/icons/Search";
import LogoCordoba from "assets/img/logo-subastas-1.png";
import Input from "@material-ui/core/Input";
import Button from "@material-ui/core/Button";
import Menu from "@material-ui/core/Menu";
import MenuItem from "@material-ui/core/MenuItem";
import MoreVertIcon from "@material-ui/icons/MoreVert";
import UseWindowDimensions from "components/homeComponents/useWindowDimensions.js";
import { HashRouter, Link } from "react-router-dom";

export default function NavbarHome(props) {
  const { height, width } = UseWindowDimensions();
  console.log(width);
  console.log(height);
  const [search, setSearch] = React.useState(" ");
  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClick = event => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const linkClass = { color: "grey" };

  const searchBar = (
    <Input
      style={{ maxWidth: "800px" }}
      placeholder="¬øQu√© buscas?"
      inputProps={{
        "aria-label": "description"
      }}
      value={search}
      onChange={e => setSearch(e.target.value)}
    />
  );
  const searchButton = (
    <HashRouter basename="/">
      <Link to={"busqueda/" + search}>
        <IconButton aria-label="search">
          <SearchIcon style={{ color: "#f68823" }} />
        </IconButton>
      </Link>
    </HashRouter>
  );

  return (
    <React.Fragment>
      <Navbar bg="light" variant="light">
        <Nav className="mr-auto">
          <HashRouter basename="/">
            <Link to="/">
              <img
                src={LogoCordoba}
                width="115.5"
                height="36"
                className="d-inline-block align-top"
                alt="React Bootstrap logo"
              />
            </Link>

            <Link to="/info" style={{ color: "grey", margin: "5px" }}>
              Info
            </Link>
            <Link to="/contacto" style={{ color: "grey", margin: "5px" }}>
              Contacto
            </Link>
          </HashRouter>
        </Nav>

        {props.searchDeactivate || width < 600 ? null : searchBar}
        {props.searchDeactivate || width < 600 ? null : searchButton}
        <div>
          <Button
            aria-controls="simple-menu"
            aria-haspopup="true"
            onClick={handleClick}
          >
            <MoreVertIcon style={{ color: "#f68823" }} />
          </Button>

          <Menu
            id="simple-menu"
            anchorEl={anchorEl}
            keepMounted
            open={Boolean(anchorEl)}
            onClose={handleClose}
          >
            {width < 600 ? (
              <MenuItem>
                {searchBar} {searchButton}
              </MenuItem>
            ) : null}

            <MenuItem style={linkClass}>
              {" "}
              <a
                href="https://subastas.justiciacordoba.gov.ar"
                style={{ color: "grey", margin: "5px" }}
              >
                {" "}
                Subastas C√≥rdoba{" "}
              </a>
            </MenuItem>
            <MenuItem style={linkClass}>
              {" "}
              <a
                href="https://subastas.justiciasanluis.gov.ar"
                style={{ color: "grey", margin: "5px" }}
              >
                {" "}
                Subastas San Luis{" "}
              </a>
            </MenuItem>
          </Menu>
        </div>
      </Navbar>
    </React.Fragment>
  );
}
